import React from "react";
import Heading from "../../../../components/Heading";
import List from "../../../../components/List";
import Section from "../../../../components/Section";
import ToolCard from "../ToolCard/ToolCard";
import "./toolsSectionList.scss";

const tools = [
  {
    image: "./images/tools/poisk.jpg",
    title: "    Google",
    description:
      "          .      ",
    price: 1999,
    termOfPerformance: 3,
  },
  {
    image: "./images/tools/KMS-web.jpg",
    title: "-  Google",
    description:
      "      .       ,      ",
    price: 1199,
    termOfPerformance: 2,
  },
  {
    image: "./images/tools/shop1.jpg",
    title: "  Google Shopping",
    description:
      "  -.    .        ",
    price: 2499,
    termOfPerformance: 3,
  },
  {
    image: "./images/tools/youtube1.jpg",
    title: "  YouTube",
    description:
      "        .       ",
    price: 2899,
    termOfPerformance: 5,
  },
];

const ToolsSectionList = () => {
  const toolCards = tools.map((tool) => (
    <ToolCard
      key={tool.title}
      img={tool.image}
      title={tool.title}
      description={tool.description}
      price={tool.price}
      termOfPerformance={tool.termOfPerformance}
    />
  ));

  return (
    <Section classes="tools" id="tools">
      <Heading> Google Ads</Heading>
      <List classes="tools__list" styleFlag={true}>
        {toolCards}
      </List>
    </Section>
  );
};

export default ToolsSectionList;
